.postItem{position:relative;border-bottom:1px solid #e8ecf3;padding-left:80px}.postItem .inner{padding:20px}.postItem header{position:relative;color:#667c99;margin-bottom:15px}.postItem header ul li{display:inline-block;margin-right:10px}.postItem header ul li.idBadge{background:#f1c40f;border-radius:3px;padding:3px 6px;line-height:1;cursor:pointer;color:#fff}.postItem header .author{position:relative}.postItem header .author h3{color:#111;font-weight:bold;font-size:1.4rem}.postItem header .avatar{position:absolute;left:-85px;line-height:64px;width:64px;height:64px}.postItem header .avatar img{width:64px;height:64px;border-radius:50%}.postItem .postBody{position:relative;font-size:1.6rem}.postItem .postBody img{display:block;max-width:100%;padding:20px;margin:0 auto 10px}.postItem .postBody p{line-height:1.6}.postItem .postBody ul, .postItem .postBody ol{padding-left:30px;margin:1rem}.postItem .postBody ul li, .postItem .postBody ol li{display:list-item;list-style:inherit;line-height:1.6}.postItem .postBody ul{list-style-type:disc}.postItem .postBody ol{list-style-type:decimal}.postItem .postBody blockquote{font-size:inherit;border:0;background:#e8ecf3;color:#667c99;border-radius:4px;padding:8px 15px;border-top:2px dotted #fff;border-bottom:2px dotted #fff;margin:1em 0}.postItem .postActions{position:relative;margin-top:-5px;text-align:right;-webkit-transition:opacity .2s;transition:opacity .2s;opacity:0;padding-right:5px}.postItem .postActions ul li{margin-right:0;margin-left:-5px;display:inline-block;vertical-align:top}.postItem:hover .postActions{opacity:1}.postItem footer{position:relative}.postItem footer ul li{line-height:2}


