.ucView{position:relative}.ucView>.container{display:-webkit-box;display:-ms-flexbox;display:flex}.container{max-width:960px}.userHero{color:#fff;text-align:center;margin-top:-1px;background:#e8ecf3;background-size:100% 100%}.userHero .darkenBg{background:rgba(0, 0, 0, .35)}.userHero .container{padding-top:40px;padding-bottom:30px}.userHero .container::before, .userHero .container::after{content:" ";display:table}.userHero .profile{text-align:left;padding-left:130px;max-width:800px;min-height:104px}.userHero .identity{display:inline;vertical-align:middle;font-size:2.2rem;margin:0;line-height:1.5;font-weight:normal}.userHero .identity a{color:#fff}.userHero .avatar{display:inline-block;-webkit-box-sizing:content-box;box-sizing:content-box;float:left;margin-left:-130px;width:96px;height:96px;border-radius:50%;font-size:4.8rem;line-height:96px;border:4px solid #fff;-webkit-box-shadow:0 2px 6px rgba(0, 0, 0, .35);box-shadow:0 2px 6px rgba(0, 0, 0, .35);background:rgba(255, 255, 255, .25);overflow:hidden}.userHero .avatar img{width:100%}.userHero .info{margin:15px 0 0;padding:0;list-style:none;font-size:1.2rem}.userHero .info li{display:inline-block;margin-right:15px}.userHero .bio{display:block !important;margin:0;font-size:1.4rem}.userHero .bio p{margin:-10px -10px 10px;border:1px dashed transparent;border-radius:4px}.userHero .lastSeen i{margin-right:5px}.tabContainer{-webkit-box-flex:1;-ms-flex:1;flex:1}


