.creationview{position:relative;padding:10px 20px;max-width:960px;margin:0 auto}.close{position:fixed;top:10px;right:10px;background:transparent;font-size:2rem;z-index:99;cursor:pointer}.header{position:relative}.header>h2{color:#667c99}.header>ul{display:-webkit-box;display:-ms-flexbox;display:flex;margin-bottom:10px}.header>ul>li{display:inline-block}.header>ul>li.channels{margin-right:5px;line-height:36px}.header>ul>li.channels .choose{color:#667c99;border:1px dotted #667c99;padding:2px;border-radius:2px;cursor:pointer}.header>ul>li.channels .channel{position:relative;color:#fff;padding:4px 12px;border-radius:3px;cursor:pointer}.header>ul>li.title{-webkit-box-flex:1;-ms-flex:1;flex:1}.header>ul>li.title h3{margin:0}.header>ul>li.title input{background:transparent;color:#667c99;font-size:1.8rem;font-weight:normal}.header>ul>li.title input:focus{border-color:transparent}.header>ul>li.title input::-webkit-input-placeholder{color:rgba(102, 124, 153, .3)}.contentEditor{position:relative}.footer{margin-top:10px;padding:10px 0}.footer .publishBtn{position:relative;min-width:100px}.footer .publishBtn i{margin-left:5px}.channelsModal{position:relative;max-width:500px;width:500px}.channelsModal>div{padding:0}.channelsModal .header{color:#667c99;background:#d7deea;font-size:1.6rem;padding:15px}.channelsModal .header h3{margin:0;font-weight:normal}.channelsModal .body{position:relative;padding:1px 0 0}.channelsModal .footer{position:relative}.channelsModal .footer button{margin:5px auto;max-width:100px;cursor:pointer}.channelsModal .channelList{overflow-y:auto;max-height:50vh}.channelsModal .channelList>li{padding:7px 20px;overflow:hidden;text-overflow:ellipsis;cursor:pointer}.channelsModal .channelList>li.active, .channelsModal .channelList>li:hover{background:#d7deea}.channelsModal .channelList .channelIcon{border-radius:4px;width:16px;height:16px;display:inline-block;vertical-align:-3px;margin-left:1px;background:#d7deea}.channelsModal .channelList .channelName{display:inline-block;width:150px;margin-right:10px;margin-left:10px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;vertical-align:top;font-size:1.4rem}.channelsModal .channelList .channelDesc{color:#667c99;font-size:1.2rem;width:370px;display:inline-block;vertical-align:top;margin-top:2px}.channelsModal .channelList>li.pinned{padding:10px 20px}.channelsModal .channelList>li.pinned .channelName{font-size:1.6rem}.channelsModal .channelList>li.pinned+li:not(.pinned){border-top:1px solid #d7deea}


