.channelsView{position:relative}.nav{padding:15px 0;overflow:auto;float:none;width:auto;padding-top:0}.nav>ul{margin-top:30px}.nav ul{display:-webkit-box;display:-ms-flexbox;display:flex}.nav ul li{line-height:36px;margin-right:20px}.nav ul li i{margin-right:5px}.nav ul li.channels{font-weight:bold}.channelsLoading{text-align:center;font-size:3rem;margin:30px auto}.channelList{position:relative;margin:15px auto}.channelList>ul{padding:0;margin:0;overflow:hidden}.channelList>ul>li{position:relative;color:#fff;width:33.33333%;float:left;height:200px;overflow:hidden}.channelList>ul>li:first-child{border-top-left-radius:4px}.channelList>ul>li:nth-child(3), .channelList>ul>li:nth-child(2):last-child, .channelList>ul>li:first-child:last-child{border-top-right-radius:4px}.channelList .channelInfo{padding:20px;text-decoration:none;position:absolute;left:0;right:0;top:0;bottom:42px;-webkit-transition:background .2s;transition:background .2s;overflow:auto}.channelList .channelInfo:hover{background:rgba(0, 0, 0, .05)}.channelList .channelInfo>h3{font-size:1.8rem;margin:0 0 10px;font-weight:bold;color:#fff}.channelList .channelInfo>p{color:rgba(255, 255, 255, .7);font-size:1.4rem;margin:0 0 10px}.channelList .subChannels{position:absolute;bottom:0;height:42px;width:calc(100% - 20px);padding:8px 0;margin:0 10px;line-height:21px;font-size:1.3rem;border-top:1px solid rgba(0, 0, 0, .05)}.channelList .subChannels>ul>li{display:inline-block;margin:0 3px}.channelList .subChannels>ul>li a{background:#fff;color:#667c99;border-radius:4px;text-transform:none;padding:5px 8px}


