.mainView{position:relative;padding:20px 30px 30px 0}.topicLoading, .postsLoading{text-align:center;font-size:3rem;margin:30px auto}.topicWrapper{padding-left:80px;border-bottom:1px solid #e8ecf3}.topicWrapper .inner{padding:20px}.topicWrapper header{color:#667c99;margin-bottom:15px}.topicWrapper header ul li{display:inline-block;margin-right:10px}.topicWrapper header ul li.idBadge{background:#f1c40f;border-radius:3px;padding:3px 6px;line-height:1;cursor:pointer;color:#fff}.topicWrapper header .author{position:relative}.topicWrapper header .author h3{color:#111;font-weight:bold;font-size:1.4rem}.topicWrapper header .avatar{position:absolute;left:-85px;line-height:64px;width:64px;height:64px}.topicWrapper header .avatar img{width:64px;height:64px;border-radius:50%}.topicWrapper .topicBody{font-size:1.6rem}.topicWrapper .topicBody img{display:block;max-width:100%;padding:20px;margin:0 auto 10px}.topicWrapper .topicBody p{line-height:1.6}.topicWrapper .topicBody ul, .topicWrapper .topicBody ol{padding-left:30px;margin:1rem}.topicWrapper .topicBody ul li, .topicWrapper .topicBody ol li{display:list-item;list-style:inherit;line-height:1.6}.topicWrapper .topicBody ul{list-style-type:disc}.topicWrapper .topicBody ol{list-style-type:decimal}.topicWrapper .topicBody blockquote{font-size:inherit;border:0;background:#e8ecf3;color:#667c99;border-radius:4px;padding:8px 15px;border-top:2px dotted #fff;border-bottom:2px dotted #fff;margin:1em 0}.topicWrapper .asideActions{position:relative;margin-top:-5px;text-align:right;-webkit-transition:opacity .2s;transition:opacity .2s;opacity:0;padding-right:5px}.topicWrapper .asideActions ul li{margin-right:0;margin-left:-5px;display:inline-block;vertical-align:top}.topicWrapper:hover .asideActions{opacity:1}.topicWrapper footer{margin:10px auto 20px;padding:10px;background:#f9fafc;border-radius:4px}.topicWrapper footer ul{text-align:right}.topicWrapper footer ul li{display:inline-block;font-size:1.5rem;margin-right:10px;cursor:pointer;color:#667c99}.topicWrapper footer ul li i{margin-right:5px}.topicWrapper footer ul li .count{margin-right:2px}.topicWrapper footer ul li.views{float:left}.commentPlaceholder{position:relative;margin-top:50px;-webkit-transition:border-color .2s;transition:border-color .2s;cursor:text;font-size:1.5rem;border:2px dashed transparent;color:#667c99;border-radius:10px;padding-left:100px;min-height:100px}.commentPlaceholder:hover{border-color:#e8ecf3}.commentPlaceholder header{position:relative;line-height:100px}.commentPlaceholder header .avatar{position:absolute;left:-85px;top:18px;line-height:64px;width:64px;height:64px}.commentPlaceholder header .avatar img{width:64px;height:64px;border-radius:50%}.commentEditorWrapper{border:1px solid #e8ecf3;border-radius:0;padding-left:0}.commentEditorWrapper:hover{border-style:solid}.commentEditorWrapper header .avatar{left:10px;top:10px;line-height:32px;z-index:999}.commentEditorWrapper header .avatar img{width:32px;height:32px}.commentEditorWrapper .commentEditBody{position:relative}.commentEditorWrapper .commentEditBody h3{margin:0 auto;font-size:1.4rem;line-height:1.5;padding:15px 10px 15px 50px}.commentEditorWrapper .commentEditBody .close{position:absolute;right:15px;top:15px;line-height:20px;cursor:pointer;opacity:.5}.commentEditorWrapper .commentEditBody .close:hover{opacity:1}.commentEditorWrapper .commentEditBody .commentEditor{line-height:1.5}.commentEditorWrapper .commentEditBody .commentEditorToolbar{border-left-color:transparent;border-right-color:transparent}.commentEditorWrapper footer{text-align:right;padding:10px 15px;border-top:1px solid #e8ecf3}.commentEditorWrapper footer button i{margin-left:5px}


